/* eslint-disable react/jsx-no-duplicate-props */
"use client"

import React, { useState, useEffect, useCallback } from "react"
import { useSearchParams } from "next/navigation"
import { Card, CardHeader, CardContent, CardTitle, CardDescription } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import {
  FileText,
  FileIcon,
  Download,
  RefreshCw,
  Upload,
  CheckCircle,
  XCircle,
  Clock,
  AlertCircle,
  Trash2,
  Loader,
} from "lucide-react"
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert"
import { Badge } from "@/components/ui/badge"
import { Progress } from "@/components/ui/progress"
import { Label } from "@/components/ui/label"
import { Input } from "@/components/ui/input"

// Types
interface DocumentValidation {
  validationStatus: "validated" | "rejected" | "pending"
  comment: string | null
}

interface SubmittedDocument {
  id: number
  idPFE: number
  fichier: string
  type: "PDF" | "Presentation"
  tutorValidation?: DocumentValidation | null
  profValidation?: DocumentValidation | null
}

// Constants
const API_BASE_URL = "http://localhost:5000"
const LIVRABLE_NAMES: Record<string, string> = {
  PDF: "Document PDF",
  Presentation: "Présentation",
}
const LIVRABLE_ICONS: Record<string, React.ReactNode> = {
  PDF: <FileText className="h-5 w-5 text-blue-500" />,
  Presentation: <FileIcon className="h-5 w-5 text-purple-500" />,
}

export default function SoumettrePage() {
  const searchParams = useSearchParams()
  const idEtudiant = searchParams.get("idEtudiant") || "4"
  const idGroupe = searchParams.get("idGroupe") || "2"

  // State
  const [pdfFile, setPdfFile] = useState<File | null>(null)
  const [presentationFile, setPresentationFile] = useState<File | null>(null)
  const [isSubmitting, setIsSubmitting] = useState(false)
  const [submitMessage, setSubmitMessage] = useState<{ type: "success" | "error"; message: string } | null>(null)
  const [submittedDocuments, setSubmittedDocuments] = useState<SubmittedDocument[]>([])
  const [isLoading, setIsLoading] = useState(true)
  const [fetchError, setFetchError] = useState<string | null>(null)
  const [uploadProgress, setUploadProgress] = useState(0)

  // Fetch documents
  const fetchDocuments = useCallback(async () => {
    setIsLoading(true)
    setFetchError(null)
    try {
      const response = await fetch(`${API_BASE_URL}/api/livrable/documents?idEtudiant=${idEtudiant}`)
      if (!response.ok) {
        const errorData = await response.json()
        throw new Error(errorData.error || "Failed to fetch documents")
      }
      const data = await response.json()
      console.log("Fetched documents:", JSON.stringify(data, null, 2))
      setSubmittedDocuments(data)
    } catch (error) {
      setFetchError(error.message)
      console.error("Fetch error:", error)
    } finally {
      setIsLoading(false)
    }
  }, [idEtudiant])

  useEffect(() => {
    fetchDocuments()
  }, [fetchDocuments])

  // File handling
  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>, setFile: (file: File | null) => void) => {
    if (e.target.files?.[0]) {
      setFile(e.target.files[0])
      simulateUploadProgress()
    }
  }

  const simulateUploadProgress = () => {
    setUploadProgress(0)
    const interval = setInterval(() => {
      setUploadProgress((prev) => {
        if (prev >= 100) {
          clearInterval(interval)
          return 100
        }
        return prev + 20
      })
    }, 100)
  }

  const resetForm = () => {
    setPdfFile(null)
    setPresentationFile(null)
    setUploadProgress(0)
    const pdfInput = document.getElementById("pdf-upload") as HTMLInputElement
    const presentationInput = document.getElementById("presentation-upload") as HTMLInputElement
    if (pdfInput) pdfInput.value = ""
    if (presentationInput) presentationInput.value = ""
  }

  // Submit documents
  const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault()
    setIsSubmitting(true)
    setSubmitMessage(null)

    if (!pdfFile || !presentationFile) {
      setSubmitMessage({ type: "error", message: "Please select both PDF and Presentation files." })
      setIsSubmitting(false)
      return
    }

    const formData = new FormData()
    formData.append("files", pdfFile)
    formData.append("files", presentationFile)
    formData.append("idEtudiant", idEtudiant)
    formData.append("idGroupe", idGroupe)

    try {
      const response = await fetch(`${API_BASE_URL}/api/livrable/submit-documents`, {
        method: "POST",
        body: formData,
      })
      if (!response.ok) {
        const errorText = await response.text()
        throw new Error(errorText || "Submission failed")
      }
      setSubmitMessage({ type: "success", message: "Documents submitted successfully!" })
      resetForm()
      fetchDocuments()
    } catch (error) {
      setSubmitMessage({ type: "error", message: `Submission error: ${error.message}` })
    } finally {
      setIsSubmitting(false)
    }
  }

  // Delete document
  const handleDelete = async (idPFE: number, id: number) => {
    if (!confirm("Are you sure you want to delete this document?")) return

    try {
      const response = await fetch(`${API_BASE_URL}/api/livrable/delete-document`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ idEtudiant, idPFE, id }),
      })
      if (!response.ok) {
        const errorText = await response.text()
        throw new Error(errorText || "Deletion failed")
      }
      fetchDocuments()
    } catch (error) {
      alert(`Deletion error: ${error.message}`)
    }
  }

  // Validation status helpers
  const getStatusIcon = (status?: string) => {
    switch (status) {
      case "validated":
        return <CheckCircle className="h-4 w-4 text-green-500" />
      case "rejected":
        return <XCircle className="h-4 w-4 text-red-500" />
      default:
        return <Clock className="h-4 w-4 text-yellow-500" />
    }
  }

  const getStatusText = (status?: string) => {
    switch (status) {
      case "validated":
        return "Validated"
      case "rejected":
        return "Rejected"
      default:
        return "Pending"
    }
  }

  const getStatusColor = (status?: string) => {
    switch (status) {
      case "validated":
        return "bg-green-100 text-green-800"
      case "rejected":
        return "bg-red-100 text-red-800"
      default:
        return "bg-yellow-100 text-yellow-800"
    }
  }

  return (
    <div className="container mx-auto py-8 px-4 max-w-5xl">
      <Card className="bg-white dark:bg-gray-800 shadow-lg rounded-xl overflow-hidden">
        <CardHeader className="bg-gradient-to-r from-blue-500 to-purple-500 text-white p-6">
          <CardTitle className="text-2xl font-bold">Submit Project Documents</CardTitle>
          <CardDescription className="text-gray-100">
            Upload your PDF and Presentation files and view feedback from your tutor and professor.
          </CardDescription>
        </CardHeader>

        <CardContent className="p-6 space-y-8">
          {/* Submission Form */}
          <section>
            <h2 className="text-xl font-semibold mb-4 flex items-center gap-2">
              <Upload className="h-5 w-5 text-blue-500" />
              Upload New Documents
            </h2>
            {submitMessage && (
              <Alert
                variant={submitMessage.type === "success" ? "default" : "destructive"}
                className={`mb-4 ${submitMessage.type === "success" ? "bg-green-50" : "bg-red-50"}`}
              >
                <AlertTitle>{submitMessage.type === "success" ? "Success" : "Error"}</AlertTitle>
                <AlertDescription>{submitMessage.message}</AlertDescription>
              </Alert>
            )}
            <form onSubmit={handleSubmit} className="space-y-6">
              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                <div className="space-y-2">
                  <Label htmlFor="pdf-upload" className="flex items-center gap-2">
                    {LIVRABLE_ICONS.PDF} PDF Document
                  </Label>
                  <Input
                    id="pdf-upload"
                    type="file"
                    accept=".pdf"
                    onChange={(e) => handleFileChange(e, setPdfFile)}
                    disabled={isSubmitting}
                    className="cursor-pointer"
                  />
                  {pdfFile && <Progress value={uploadProgress} className="h-2" />}
                </div>
                <div className="space-y-2">
                  <Label htmlFor="presentation-upload" className="flex items-center gap-2">
                    {LIVRABLE_ICONS.Presentation} Presentation
                  </Label>
                  <Input
                    id="presentation-upload"
                    type="file"
                    accept=".ppt,.pptx,.pdf"
                    onChange={(e) => handleFileChange(e, setPresentationFile)}
                    disabled={isSubmitting}
                    className="cursor-pointer"
                  />
                  {presentationFile && <Progress value={uploadProgress} className="h-2" />}
                </div>
              </div>
              <Button
                type="submit"
                disabled={isSubmitting}
                className="w-full bg-blue-500 hover:bg-blue-600 text-white"
              >
                {isSubmitting ? (
                  <Loader className="h-5 w-5 animate-spin mr-2" />
                ) : (
                  <Upload className="h-5 w-5 mr-2" />
                )}
                {isSubmitting ? "Submitting..." : "Submit Documents"}
              </Button>
            </form>
          </section>

          {/* Submitted Documents */}
          <section>
            <div className="flex items-center justify-between mb-4">
              <h2 className="text-xl font-semibold flex items-center gap-2">
                <FileText className="h-5 w-5 text-purple-500" />
                Submitted Documents
              </h2>
              <Button
                variant="outline"
                onClick={fetchDocuments}
                disabled={isLoading}
                className="flex items-center gap-2"
              >
                <RefreshCw className={`h-4 w-4 ${isLoading ? "animate-spin" : ""}`} />
                Refresh
              </Button>
            </div>

            {isLoading ? (
              <div className="flex justify-center items-center py-8">
                <Loader className="h-6 w-6 animate-spin text-blue-500" />
                <span className="ml-2 text-gray-600">Loading...</span>
              </div>
            ) : fetchError ? (
              <Alert variant="destructive" className="bg-red-50">
                <AlertCircle className="h-4 w-4" />
                <AlertTitle>Error</AlertTitle>
                <AlertDescription>{fetchError}</AlertDescription>
              </Alert>
            ) : submittedDocuments.length === 0 ? (
              <div className="text-center py-8 text-gray-500">
                <Upload className="h-8 w-8 mx-auto mb-2" />
                <p>No documents submitted yet.</p>
              </div>
            ) : (
              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                {submittedDocuments.map((doc) => (
                  <Card key={`${doc.idPFE}-${doc.id}`} className="shadow-md hover:shadow-lg transition-shadow">
                    <CardHeader className="bg-gray-50 dark:bg-gray-700 flex items-center justify-between">
                      <div className="flex items-center gap-3">
                        <div className="p-2 bg-blue-100 rounded-full">
                          {LIVRABLE_ICONS[doc.type] || <FileText className="h-5 w-5" />}
                        </div>
                        <div>
                          <h3 className="font-medium">{LIVRABLE_NAMES[doc.type]}</h3>
                          <Badge variant="secondary" className="mt-1">Submitted</Badge>
                        </div>
                      </div>
                      <div className="flex gap-2">
                        <Button variant="ghost" size="sm" asChild>
                          <a href={`${API_BASE_URL}${doc.fichier}`} download>
                            <Download className="h-4 w-4" />
                          </a>
                        </Button>
                        <Button
                          variant="ghost"
                          size="sm"
                          onClick={() => handleDelete(doc.idPFE, doc.id)}
                          className="text-red-500 hover:text-red-700"
                        >
                          <Trash2 className="h-4 w-4" />
                        </Button>
                      </div>
                    </CardHeader>
                    <CardContent className="p-4 space-y-4">
                      {/* Tutor Validation */}
                      <div className="space-y-2">
                        <div className="flex items-center gap-2">
                          {getStatusIcon(doc.tutorValidation?.validationStatus)}
                          <span className="font-semibold">Tutor</span>
                        </div>
                        <Badge className={getStatusColor(doc.tutorValidation?.validationStatus)}>
                          {getStatusText(doc.tutorValidation?.validationStatus)}
                        </Badge>
                        {doc.tutorValidation?.comment ? (
                          <p className="text-sm text-gray-600 italic">"{doc.tutorValidation.comment}"</p>
                        ) : (
                          <p className="text-sm text-gray-500">No comment provided</p>
                        )}
                      </div>
                      {/* Professor Validation */}
                      <div className="space-y-2">
                        <div className="flex items-center gap-2">
                          {getStatusIcon(doc.profValidation?.validationStatus)}
                          <span className="font-semibold">Professor</span>
                        </div>
                        <Badge className={getStatusColor(doc.profValidation?.validationStatus)}>
                          {getStatusText(doc.profValidation?.validationStatus)}
                        </Badge>
                        {doc.profValidation?.comment ? (
                          <p className="text-sm text-gray-600 italic">"{doc.profValidation.comment}"</p>
                        ) : (
                          <p className="text-sm text-gray-500">No comment provided</p>
                        )}
                      </div>
                    </CardContent>
                  </Card>
                ))}
              </div>
            )}
          </section>
        </CardContent>
      </Card>
    </div>
  )
}